<template>
        <div class="faqWrap" @click="faqSearchRef.closeSearchList()">
                <div class="faq" @click.stop="stop">
                        <faqTitle :context="FAQ_DATA[lang]?.title" :keyWords="[FAQ_DATA[lang]?.titleKey]"
                                @click="faqSearchRef.closeSearchList()"></faqTitle>
                        <faqSearch ref="faqSearchRef" :list="searchList"></faqSearch>
                        <faqContent :data="contentData"></faqContent>
                </div>
        </div>
</template>
<script setup>
import faqTitle from "./components/title.vue";
import faqSearch from "./components/search.vue";
import faqContent from "./components/content.vue";
import { FAQ_DATA } from "@/data/faq";
import { stop } from "@/utils/tools"
import { computed } from 'vue'
const faqSearchRef = ref()
const i18n = useI18n()
const lang = computed(() => {
        return i18n.locale.value
})
const searchList = ref([])
const contentData = ref([])
const initData = () => {
        let data = FAQ_DATA[lang.value]
        contentData.value = data
        let arr = []
        for (let i in data.modules) {
                arr = arr.concat([...data.modules[i].qaList])
        }
        searchList.value = arr
}
initData()
</script>
<style scoped lang="scss">
.faqWrap {
        width: 100%;
        @include dfjc;
}

.faq {
        margin: 120px 0;
        width: 940px;
}

@media screen and (max-width: 1024px) {
        .faq {
                margin: 5.3333vw 0;
                width: 91.2vw;
        }

}
</style>